import Vue from 'vue'
import App from './App.vue'
import web3Plugin from './plugins/web3'
import router from './router.js'
import VueRouter from 'vue-router'
import { store } from './store/index'

Vue.use(VueRouter)
Vue.config.productionTip = false

// web3contracts
Vue.use(web3Plugin);
// vue-router

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
