<template>
  <div id="sponsored">
    <a-divider></a-divider>
    <div class="button-wrapper">
      <a-button class="editable-add-btn" @click="openModal"></a-button>
      <a-button type="primary" @click="openContestInfoModal" style="margin-left: 20px;"></a-button>
    </div>
    <a-table  :dataSource="newContestData" :columns="columns"
      rowKey="id" style="clear: both;">
      <template slot="operation" slot-scope="text, record">
        <a-popconfirm
          v-if="newContestData.length"
          title="?"
          @confirm="() => onDelete(record)">
          <a-button type="danger">Delete</a-button>
        </a-popconfirm>
      </template>
    </a-table>
    <a-modal
      title=""
      :visible="addChoiceModalVisible"
      @ok="handleAddChoice"
      :confirmLoading="confirmLoading"
      @cancel="handleCancelAddChoice">
      <a-form layout='vertical' :form="form">
        <a-form-item label=''>
          <a-input
            v-decorator="[
              'title',
              {
                rules: [{ required: true, message: '' }],
              }
            ]"
          />
        </a-form-item>
        <a-form-item label='A'>
          <a-input
            v-decorator="[
              'choiceA',
              {
                rules: [{ required: true, message: 'A' }],
              }
            ]"
          />
        </a-form-item>
        <a-form-item label='B'>
          <a-input
            v-decorator="[
              'choiceB',
              {
                rules: [{ required: true, message: 'B' }],
              }
            ]"
          />
        </a-form-item>
        <a-form-item label='C'>
          <a-input
            v-decorator="[
              'choiceC',
              {
                rules: [{ required: true, message: 'C' }],
              }
            ]"
          />
        </a-form-item>
        <a-form-item label=''>
          <a-radio-group name="radioGroup" v-decorator="['answer', {rules: [{ required: true, message: '' }]}]">
            <a-radio :value="0">A</a-radio>
            <a-radio :value="1">B</a-radio>
            <a-radio :value="2">C</a-radio>
          </a-radio-group>
        </a-form-item>
      </a-form>
    </a-modal>
    <contest-info-form ref="contestForm" @submit="handleSubmitSponsor"></contest-info-form>
  </div>
</template>

<script>
import {
  Divider,
  Button,
  Table,
  Popconfirm,
  Modal,
  Input,
  Icon,
  InputNumber,
  DatePicker,
  Form,
  Radio
} from 'ant-design-vue'
import { setTimeout } from 'timers';
import ContestInfoForm from './share/ContestInfoForm';
const moment = require('moment');
import { notification } from 'ant-design-vue';

export default {
  name: 'sponsored',
  props: {
    // msg: String
  },
  components: {
    ADivider: Divider,
    AButton: Button,
    ATable: Table,
    AModal: Modal,
    APopconfirm: Popconfirm,
    AForm: Form,
    AFormItem: Form.Item,
    AInput: Input,
    AIcon: Icon,
    AInputNumber: InputNumber,
    ARadio: Radio,
    ARadioGroup: Radio.Group,
    ADatePicker: DatePicker,
    ContestInfoForm
  },
  data () {
    return {
      newContestData: [{
        id: 0,
        title: '',
        choice: [
          '',
          '',
          ''
        ],
        answer: 2
      },{
        id: 1,
        title: '',
        choice: [
          '300400',
          '',
          '400600'
        ],
        answer: 0
      }, {
        id: 2,
        title: '',
        choice: [
          '',
          '',
          ''
        ],
        answer: 2
      }],
      columns: [{
        title: '',
        dataIndex: 'id',
        width: '30%'
      }, {
        title: '',
        dataIndex: 'title',
      }, {
        title: '',
        dataIndex: 'operation',
        scopedSlots: { customRender: 'operation' }
      }],
      submitLoading: false,
      addChoiceModalVisible: false,
      confirmLoading: false,
      form: this.$form.createForm(this),
      newContestAnswer: {},  // contest
    }
  },
  computed: {
    // contest
    newContestContent: function () {
      return this.newContestData.map(({id, title, choice}) => {
        return { id, title, choice }
      });
    }
  },
  methods: {
    // modal
    openModal () {
      this.addChoiceModalVisible = true;
    },
    // 
    handleSubmitSponsor (payload) {
      let that = this;
      this.submitLoading = true;
      this.newContestData.forEach(item => {
        this.newContestAnswer[item.id] = item.answer;
      })
      // payload contest    
      payload.ddl = payload.ddl.valueOf();
      payload.content = JSON.stringify(this.newContestContent).replace(/\"/g,"'");
      payload.answer = JSON.stringify(this.newContestAnswer).replace(/\"/g,"'");
      console.log(payload);

      // 
      this.newContestAnswer = {};

      let contracts = this.$contracts;
      this.$web3.eth.getAccounts(function(error, accounts) {
        if (error) {
          console.log(error);
        }
        var account = accounts[0];

        contracts.Playground.deployed().then(function(instance) {
          console.log(instance);
          return instance.addContest.sendTransaction(payload.title, payload.desc, payload.ddl, payload.cost, payload.bonus, payload.content, payload.answer, {from: account, value: 2000 + 100 + payload.bonus});
        }).then(function(txhash) {
          console.log(txhash);
          that.submitLoading = false;
          that.$refs.contestForm.handleCancelSubmit();
          notification.open({
						message: '',
						description: '',
						icon: <a-icon type="like" style="color: #108ee9" />,
					});
        }).catch(function(err) {
          console.log(err);
          that.submitLoading = false;
          that.$refs.contestForm.handleCancelSubmit();
          notification.open({
						message: '',
						description: '',
						icon: <a-icon type="close-circle" style="color: #f44336" />,
					});
        });
      });
    },
    // 
    onDelete(record) {
      this.newContestData.splice(record.id,1);
      let count = 0;
      this.newContestData.forEach((item) => {
        item.id = count++;
      })
    },
    handleAddChoice () {
      this.confirmLoading = true;
      this.form.validateFields((err, values) => {
        if (err) {
          this.confirmLoading = false;
          return;
        }
        console.log(values);
        this.newContestData.push({
          id: this.newContestData.length,
          title: values.title,
          choice: [ values.choiceA, values.choiceB, values.choiceC ],
          answer: values.answer
        })
        this.form.resetFields();
        this.addChoiceModalVisible = false;
        this.confirmLoading = false;
      })
    },
    handleCancelAddChoice () {
      this.addChoiceModalVisible = false;
      this.resetForm();
    },
    resetForm () {
      this.form.resetFields()
    },
    openContestInfoModal () {
      this.$refs.contestForm.openContestInfoModal();
    }
  }
}
</script>

