<template>
	<a-modal
		title=""
		:visible="contestInfoModalVisible"
		@ok="handleSubmitInfo"
		@cancel="handleCancelSubmit">
		
		<a-form layout='vertical' :form="form">
			<a-form-item label=''>
				<a-input
					placeholder=""
					v-decorator="[
						'title',
						{
							rules: [{ required: true, message: '' }],
						}
					]"
				/>
			</a-form-item>
			<a-form-item label=''>
				<a-input
					placeholder=""
					v-decorator="[
						'desc',
						{
							rules: [{ required: true, message: '' }],
						}
					]"
				/>
			</a-form-item>
			<a-form-item label="">
				<a-date-picker @change="onDatePick" v-decorator="['ddl', {rules: [{ required: true, message: '' }]}]"/>
			</a-form-item>
			<a-form-item label="">
				<a-input-number 
					placeholder=""
					v-decorator="['cost', {rules: [{ required: true, message: '~' }]}]"
				/>
			</a-form-item>
			<a-form-item label="">
				<a-input-number
